import axios from 'axios';
import Inferno from 'inferno';
import Component from 'inferno-component';
import logo from './logo.svg';
import './App.css';
import qs from 'qs';
import ParameterInput from './components/ParameterInput.jsx';
import CsvDownloadButton from './components/CsvDownloadButton.jsx';

class App extends Component {
  constructor(){
    super();
    this.state = {
      data : [],
      webserviceName : '',
      parameters : [],
      parametersLoaded : false,
      sort : {
        key : null,
        asc : null
      }
    };
  }
  componentDidMount() {
    var urlQuery = {};
    if (window.location.search !== ''){
      window.location.search.replace(/\?/, '').split('&').map(function(val){
        urlQuery[decodeURIComponent(val.split('=')[0])] = decodeURIComponent(val.split('=')[1]);
      })
    }
    urlQuery['auth_token'] = localStorage.colAuthToken;
    var upperUrlQuery = {};
    for (var key in urlQuery){
      upperUrlQuery[key.toUpperCase()] = urlQuery[key]
    }
    if (!upperUrlQuery['WEBSERVICE']){
      urlQuery['webservice'] = window.location.hash.replace(/#/,'');
      upperUrlQuery['WEBSERVICE'] = window.location.hash.replace(/#/,'');
    }
    this.setState({webserviceName : upperUrlQuery['WEBSERVICE']});
    axios.post('https://ax1vnode1.cityoflewisville.com/v2/', qs.stringify({
      webservice : 'ITS/Get Parameters For Webservice',
      webservice_name : this.state.webserviceName,
      auth_token : localStorage.colAuthToken
    }))
    .then((e) => {
      var data = e.data;
      var countOfParametersPreFilled = 0;
      if (data.length === 0){
        data[0] = [];
      }
      if(data[0].length){
        data[0].map((val, i) => {
          if (upperUrlQuery[val['name'].replace(/@/, '').toUpperCase()]){
            data[0][i]['value'] = upperUrlQuery[val['name'].replace(/@/, '').toUpperCase()];
            countOfParametersPreFilled += 1;
          }else{
            const textTypes = ['char', 'nvarchar', 'varchar', 'varbinary'];
            if (textTypes.indexOf(val['PARAMETER_TYPE']) > -1){
              data[0][i]['value'] = '';
            }else{
              data[0][i]['value'] = null;
            }
          }
        })
      }
      this.setState({parameters : data[0]});
      this.setState({parametersLoaded : true});
      if (countOfParametersPreFilled === data[0].length){
        this.getData();
      }
    })
  }

  formatCellData(value){
    if (typeof value !== 'string'){
      return value;
    }else{
      if (value.substring(0,4) === '/9j/'){
        return <img src={'data:image/jpg;base64,' + value} className="img-responsive"></img>
      }else if (value.substring(0,4) === 'http'){
        const imageFileExtensions = ['.jpg', '.png', '.tiff', '.gif', '.webp', '.bpg'];
        if (imageFileExtensions.indexOf(value.substring(value.length - 4).toLowerCase()) > -1){
          return <img src={value} className="img-responsive"></img>
        }
        else{
          return <a href={value}>{value}</a>
        }
      }else{
        return value
      }
    }
  }

  formatHeaderButton(header, val){
    if (this.state.sort.key !== header){
       return <th><button className="btn" onClick={() => {this.sortData(val, header)}}>{header}</button></th>
    }else{
      if (this.state.sort.asc){
         return <th><button className="btn btn-info" onClick={() => {this.sortData(val, header)}}>{header} <span class="glyphicon glyphicon-triangle-top"></span></button></th>
      }else{
         return <th><button className="btn btn-info" onClick={() => {this.sortData(val, header)}}>{header} <span class="glyphicon glyphicon-triangle-bottom"></span></button></th>
      }
    }
  }

  getData(ev){
    if(ev){
      ev.preventDefault();
    }
    var formData = {webservice : this.state.webserviceName, auth_token : localStorage.colAuthToken};
    this.state.parameters.map( (val) => {
      if (val['PARAMETER_TYPE'] === 'datetime' && val['value']){
        val['value'] = val['value'].replace(/T/, '');
      }
      formData[val['name'].replace(/@/, '')] = val['value'];
    });
    axios.post('https://ax1vnode1.cityoflewisville.com/v2/', qs.stringify(formData))
    .then( (e) => {
      var data = e.data;
      this.setState({data});
      this.setState({sort : {
        key : null,
        asc : null
      }})
    })
  }

  sortData(resultSet, key){
    let data = this.state.data;
    if ((this.state.sort.key === key && !this.state.sort.asc) || this.state.sort.key !== key){
      data[resultSet] = data[resultSet].sort((a,b) => {
          // sort ascending
          if (typeof a[key] === 'number'){
            return a[key] - b[key];
          }else if (typeof a[key] === 'string'){
            let c = a[key] || '';
            let d = b[key] || '';
            c = c.toUpperCase();
            d = d.toUpperCase();
            if (c < d) {
              return -1;
            }else if (c > d) {
              return 1;
            }else{
              return 0;
            }
          }
      })
    }else{
      data[resultSet] = data[resultSet].sort((a,b) => {
          // sort descending 
          if (typeof a[key] === 'number'){
            return b[key] - a[key];
          }else if (typeof a[key] === 'string'){
            let c = a[key] || '';
            let d = b[key] || '';
            c = c.toUpperCase();
            d = d.toUpperCase();
            if (c > d) {
              return -1;
            }else if (c < d) {
              return 1;
            }else{
              return 0;
            }
          }
      })
    }
    if (this.state.sort.key === key){
      // this.state.sort.asc = !this.state.sort.asc;
      this.setState({sort : {key : key, asc : !this.state.sort.asc}})
    }else{
      this.setState({
        sort : {
          key : key,
          asc : true
        }
      })
      this.state.sort.key = key;
      this.state.sort.asc = true;
    }
    this.setState({data});
  }

  updateParameterValue(parameter, newValue){
    var parameters = this.state.parameters;
    parameters.map((val, index) => {
      if (val['name'] === parameter['name']){
        parameters[index]['value'] = newValue;
      }
    })
    this.setState({parameters});
  }

  render() {
    return (
      <div className="col-sm-offset-1 col-sm-10">
        <h1>{this.state.webserviceName}</h1>
        <form className="form-inline">
          {this.state.parameters.map((val) => {
            return (<ParameterInput parameter={val} updateFunction={this.updateParameterValue.bind(this)} />)
          })}
          <div className="row">
            <button className="btn btn-success" style="margin-left : 15px" type="submit" onClick={this.getData.bind(this)}>Submit</button>
          </div>
        </form>
        {Object.keys(this.state.data).map( (val) => {
          return (
            <div>
              <h2>{val}</h2>
              <CsvDownloadButton jsonData={this.state.data[val]} title={val} />
              <table className="table table-striped table-bordered table-responsive">
                <thead>
                  <tr>
                    {Object.keys(this.state.data[val][0]).map( (header) => {
                      return this.formatHeaderButton(header, val)
                    })}
                  </tr>
                </thead>
                <tbody>
                  {this.state.data[val].map( (row) => {
                    return (
                      <tr> {Object.keys(row).map( (cell) => {
                        return <td>{this.formatCellData(row[cell])}</td>
                      })} </tr>
                    )
                  })}
                </tbody>
              </table>
            </div>
          )
        })}
      </div>
    );
  }
}

export default App;



